@import url("https://fonts.googleapis.com/css2?family=Open+Sans&family=Poppins:wght@400;700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  --clr-violet: hsl(257, 40%, 49%);
  --fw-light: 400;
  --fw-heavy: 700;
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  color: white;
  height: 100vh;
  background-image: url(images/bg-desktop.svg);
  background-position: cover;
  background-repeat: no-repeat;
  background-color: var(--clr-violet);
}

img {
  max-width: 100%;
}

ul {
  padding: 0px;
}

h1 {
  font-family: "Poppins", sans-serif;
}

.navbar {
  margin: 2em 5%;
}

main {
  margin: 0 auto;
  padding-top: 3em;
  width: 90%;
  display: flex;
  align-items: flex-start;
}

.container {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-left: 1em;
}
.container h1 {
  font-size: 2.2rem;
}
@media (max-width: 1050px) {
  .container h1 {
    font-size: 1.8rem;
  }
}
.container p {
  font-weight: var(--fw-light);
  line-height: 1.6;
}
.container .btn {
  width: 40%;
  padding: 0.8em;
  font-size: 1rem;
  color: var(--clr-violet);
  font-family: "Open Sans", sans-serif;
  border: none;
  border-radius: 30px;
  box-shadow: 2px 2px 10px #261369;
  cursor: pointer;
}
.container .btn:hover {
  background: #261369;
  color: white;
  border: 1px solid white;
}

.img-container {
  flex: 1.2;
}

.social-icons {
  align-self: flex-end;
  position: absolute;
  bottom: 0;
  list-style: none;
}
.social-icons li {
  display: inline-flex;
  margin-right: 0.5em;
  padding: 0.5em;
  width: 40px;
  border: 1px solid white;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
}
.social-icons a {
  text-decoration: none;
  color: white;
}

@media (max-width: 700px) {
  body {
    background-image: url("images/bg-mobile.svg");
    background-size: cover;
    background-position: top;
  }

  .logo {
    width: 40%;
  }

  main {
    flex-direction: column;
    text-align: center;
    width: 85%;
    padding-top: 1em;
  }

  button.btn {
    width: 80% !important;
    margin: 1em auto;
  }

  .social-icons {
    margin-top: 2em;
    position: relative;
    align-self: center;
  }
}

